:root {
  --accent-color: #e50914;
  --dark-color: #141414;
  --light-color: #f4f4f4;
  --background-color: #181818;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Verdana', 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', 'sans-serif';
}

a {
  color: #000000;
  text-decoration: none;
}
